/* @import url("https://fonts.google.com/specimen/Poppins"); */
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
body {
    margin: 0;
    color: rgba(0, 0, 0, 0.87);
    font-size: 1rem;
    line-height: 3.5;
    letter-spacing: 0.00938em;
    background-color: #fff;
   
}
.button-text {
    position: absolute;
    word-wrap: break-word;
    bottom: -3%;
    width: 100%;
    left: 0%;
    font-size: 15rem;
    font-weight: bold;
    line-height: 0.95;
 }

 .square-button {
    width: 198px;
    height: 198px;
    float: left;
    position: relative;
 }
 .circle {
    margin-left: 0px;
    margin-top: 0px;
    display: flex;
    width: 75vh;
    height: 68vh;
    background: radial-gradient(#ffb78d, #F5CEB1);
    border-radius: 0% 0% 90% 0%;
}
.circle1 {
    margin-left: -76px;
    margin-top: -117px;
    display: flex;
    width: 70vh;
    height: 69vh;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

    background-color: #E4E4E4;
    border-radius: 0% 0% 73% 0%;
}

.btn{
    background-color: #da753a;
    width: 120px;
}
